<template>
	<view style="width: 100%;height: 100%;justify-content: center;align-items: center;display: flex;"
		v-if="isShowLogin">
		<text
			style="width: 100px;height: 30px;background-color: #FF5725;color: #FFFFFF;text-align: center;font-size: 15px;line-height: 30px;border-radius: 15px;"
			@click="doLongin()">{{i18n.mine.qingdenglu}}</text>
	</view>
	<view style="width: 100%;height: 100%;" v-else>

		<view style="width: 100%;position: relative;" :style="'height:' + system_top + 'px;'">
			<view class="" style="text-align: center;font-weight: bold;height: 40px;line-height: 40px;position: absolute;left: 0;right: 0;bottom: 0;" >
				消息中心
			</view>
		</view>
		<view style="width: 100%;height: 42px;" class="row">
			<view v-for="(item,index) in tabbar" :key="index" :class="index == tabIndex ? 'tab_sel' : 'tab_nor'"
				@click="tabbarClick(index)">
				{{item}}
			</view>
		</view>
		<timlist v-if="tabIndex == 0"></timlist>
		<sysList v-if="tabIndex == 1" @gotoCell="gotoCell" :height="scrollHeight"></sysList>
	</view>

</template>

<script>
	import timlist from '../TUI-Conversation/conversation/conversation.vue'
	import sysList from "@/components/systemMsg/index.vue"
	export default {
		components: {
			timlist,
			sysList
		},
		data() {
			return {
				system_top: 40,
				scrollHeight: 0,
				tabbar: [],
				tabIndex: 0,
				isShowLogin: false
			}
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		onReady() {
			const res = uni.getSystemInfoSync();

			// #ifdef MP-WEIXIN
			let weinfo = uni.getMenuButtonBoundingClientRect();
			this.weixinMenuWidth = weinfo.width + 12
			// #endif
			this.system_top = parseInt(res.safeArea.top) + 40
			this.scrollHeight = res.windowHeight - 42 - this.system_top
			this.tabbar = [this.i18n.message.liaotian, this.i18n.message.tongzhi]
		},
		onShow() {
			var that = this
			var value = uni.getStorageSync('userinfo');
			if (!value || !value.id || value.id == 0) {
				that.isShowLogin = true
			} else {
				that.isShowLogin = false
			}
			uni.$emit('TUIchatsetMessageRead', '1')
		},
		methods: {
			tabbarClick(index) {
				this.tabIndex = index
				
				
			},
			gotoCell(item) {

				console.log(item)
				uni.navigateTo({
					url: 'systemMsg?id=' + item.id
				})
			},
			doLongin() {
				uni.navigateTo({
					url: '../login/login',
				});
			}
		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		overflow: hidden;
		background-color: #fff;
	}

	.tab_sel {
		font-size: 16px;
		font-weight: bold;
		color: #FF5725;
		position: relative;
		height: 42px;
		justify-content: center;
		width: 50%;
		text-align: center;
	}

	.tab_sel::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		height: 2px;
		width: 100%;
		background-color: #FF5725;
	}

	.tab_nor {
		font-size: 16px;
		font-weight: bold;
		color: #6F6F6F;
		position: relative;
		height: 30px;
		width: 50%;
		text-align: center;
	}
</style>
